import React from "react";

// UI Elements
import { CRS, latLng, LatLng, LatLngBounds } from "leaflet";
import { ImageOverlay, Map, Polyline } from "react-leaflet";
import EncounterDialog from "../components/EncounterDialog";
import VertexMarker from "../components/VertexMarker";
import HeroMarker from "../components/HeroMarker";

// Pathfinding imports
import npath from "ngraph.path";
import { Node } from "ngraph.graph";

// Models
import GameState from "../models/GameState";
import Queue from "../models/Queue";
import { MapGraph, NodeData } from "../models/MapGraph";

// Services
import MockGameStateService from "../services/MockGameStateService";

type OverheadMapProps = {
    startState: GameState;
    graph: MapGraph; // The map graph data
    mapUrl: string;
    pathColor: string; // The default path colour is brown
    onNewMap: (state: GameState) => void; // Fire when a new map load is needed
};

const OverheadMap: React.FunctionComponent<OverheadMapProps> = (props: OverheadMapProps) => {
    const { startState, graph, mapUrl, pathColor, onNewMap } = props;

    let [pos, setPos] = React.useState<LatLng | null>(null); // The current (target) position of the player marker
    let [path, setPath] = React.useState<LatLng[]>([]); // The path to the hovered vertex
    let [vertexQueue] = React.useState(new Queue<Node<NodeData>>()); // A queue of points to visit during path-finding
    let [encounterId, setEncounterId] = React.useState<string | null>(null);

    let [gameState, setGameState] = React.useState<GameState>(startState);
    let [initialNode, setInitialNode] = React.useState(true); // Is it the first node arrived on a new map (So that we don't bounce between maps)

    // Fire when marker move has finished
    const handleMoveEnd = (name: string) => {
        console.log("Move ended : " + name);

        const targetVertex = vertexQueue.pop();
        if (targetVertex) {
            console.log(`Next vertex ${targetVertex.id}`);

            setPos(targetVertex.data.position);

            // Update user state
            gameState.currentVertextId = targetVertex.id.toString();
            setGameState(gameState);

            // Activate exit if there is one, move to new map
            if (targetVertex.data.exitId && !initialNode) {
                console.log(`Set exit ${targetVertex.data.exitId}`);
                const splits = targetVertex.data.exitId.split(",");
                if (splits.length === 2) {
                    const newState: GameState = {
                        currentMapId: splits[0],
                        currentVertextId: splits[1],
                    };

                    vertexQueue.clear();
                    onNewMap(newState);
                }
            } else if (vertexQueue.length() <= 0) {
                // Save the movement if its the last move
                const stateService = new MockGameStateService();
                const newState = stateService.getState();
                newState.currentVertextId = targetVertex.id.toString();
                stateService.setState(newState);
            }

            // Activate encounter if there is one
            if (targetVertex.data.encounterId) {
                // console.log(`Set encounter ${targetEncounterId}`);
                setEncounterId(targetVertex.data.encounterId);
            }

            setInitialNode(false);
        }
    };

    // Fire when user clicks on the map
    const handleClick = (e: any) => {
        console.log(`clicked at: ${Math.round(e.latlng.lat)},${Math.round(e.latlng.lng)}`);
    };

    const onClick = (vertexId: string) => {
        console.log(`Clicked on vertex ${vertexId}`);

        var pathFinder = npath.aStar(graph.getNGraph());
        let path = pathFinder.find(gameState.currentVertextId, vertexId);

        if (path.length <= 1) {
            console.log(`No route found to ${vertexId}`);
            return;
        }

        path.pop(); // Remove the current/start node

        vertexQueue.clear();
        let nextVertex = path.pop();
        while (nextVertex) {
            vertexQueue.push(nextVertex);
            // Stop if a vertex with an an encounter is hit
            if (nextVertex.data.encounterId) {
                break;
            }
            nextVertex = path.pop();
        }

        handleMoveEnd("party"); // Trigger the move to the next vertex
    };

    // On hover, show the path to the marker
    const onHover = (vertexId: string) => {
        var pathFinder = npath.aStar(graph.getNGraph());
        let path = pathFinder.find(gameState.currentVertextId, vertexId);

        let pathVertexes = path.map((p) => p.data.position);
        setPath(pathVertexes);
    };

    // Move to a vertex
    const moveToVertexId = (mapGraph: MapGraph, vertexId: string) => {
        if (!mapGraph) return;

        const match = mapGraph.findVertex(vertexId);
        if (match) {
            vertexQueue.push(match);
            handleMoveEnd("party");
        } else {
            console.error(`Can't find vertex ${vertexId}`);
        }
    };

    // On encounter dialog closed
    const handleEncounterClose = (moveTo: string | undefined) => {
        console.log("Close encounter");
        setEncounterId(null);

        if (moveTo) {
            moveToVertexId(graph, moveTo);
        }
    };

    React.useEffect(() => {
        if (initialNode) {

            console.log("Initial node : " + gameState.currentMapId + ", " + gameState.currentVertextId);

            // Mpve to the initial node
            moveToVertexId(graph, gameState.currentVertextId);
            setInitialNode(false);
        }
    });

    const bounds = new LatLngBounds(latLng([0.0, 0.0]), latLng([3200, 4000]));

    if (!pos) return null;

    const renderMarkers = (graph: MapGraph) =>
        graph
            .getVertices()
            .map((v) => <VertexMarker key={v.id} vertex={v} onHover={onHover} onHoverEnd={() => setPath([])} onClick={onClick} />);

    return (
        <Map
            crs={CRS.Simple}
            zoom={-1}
            minZoom={-1}
            maxZoom={-1}
            bounds={bounds}
            maxBounds={bounds}
            scrollWheelZoom={false}
            onClick={handleClick}
            zoomControl={false}
        >
            <EncounterDialog onClose={handleEncounterClose} encounterId={encounterId} />
            {/* <TileLayer url="tiles/{z}/{x}/{y}.png" bounds={bounds} /> */}
            <ImageOverlay url={mapUrl} bounds={bounds} />
            <HeroMarker name="party" position={pos} onMoveEnd={handleMoveEnd} />
            {renderMarkers(graph)}
            <Polyline positions={path} color={pathColor} dashArray="5,10,3,7,11,9" weight={4} dashOffset="0" />
        </Map>
    );
};

export default OverheadMap;
