import React from "react";
import { useHistory } from "react-router-dom";
import { makeStyles, Typography } from "@material-ui/core";

import LinkTo from "../components/LinkTo";
import KeyMaps from "../components/KeyMaps";

import MockSaveService from "../services/MockSaveService";
import SaveGameSelector from "../components/SaveGameSelector";
import MockPartyService from "../services/MockPartyService";
import MockGameStateService from "../services/MockGameStateService";

const useStyles = makeStyles({
    root: {
        backgroundColor: "#282c34",
        height: "100vh",
    },
    close: {
        filter: "invert(60%)",
    },
    titleContainer: {
        padding: "10vh",
    },
    appLogo: {
        height: "10vh",
        pointerEvents: "none",
        padding: "20px",
    },
    chooseText: {
        color: "white",
    },
});

const GameLoadMenu: React.FunctionComponent = () => {
    const classes = useStyles();

    const [saveTimes, setSaveTimes] = React.useState<undefined | (Date | null)[]>(undefined);
    const [currentSave, setCurrentSave] = React.useState(0);

    React.useEffect(() => {
        // Initial load of hero data
        const loadSaves = () => {
            const saveService = new MockSaveService();
            setSaveTimes(saveService.getSlotUpdateTimes());
            setCurrentSave(saveService.getCurrentSave());
        };

        if (!saveTimes) {
            loadSaves();
        }
    });

    let history = useHistory();
    const navigateToHomeMap = () => history.push("/");

    if (!saveTimes) return null;

    const saveSelected = (slot: number) => {
        console.log("Loaing slot : " + slot);

        const saveService = new MockSaveService();
        saveService.setCurrentSave(slot);

        const stateService = new MockGameStateService();
        stateService.clearCache();
        const partyService = new MockPartyService();
        partyService.clearCache();

        navigateToHomeMap();
    };

    // Can choose any non-empty slots, except the current one
    const calculateEnabledSlots = (saves: (Date | null)[]): boolean[] => {
        const enabledSlots = saves.map((s) => s !== null);
        console.log("Games : " + JSON.stringify(enabledSlots));
        enabledSlots[currentSave] = false;
        return enabledSlots;
    };

    return (
        <KeyMaps backUrl="/menu">
            <div className={classes.root}>
                <LinkTo url={`${process.env.PUBLIC_URL}/menu`} top="20px" left="20px" width="40px">
                    <img src="images/close.svg" className={classes.close} alt="close" />
                </LinkTo>
                <div className={classes.titleContainer}>
                    <img src="./volcano.svg" className={classes.appLogo} alt="logo" />
                    <img className="title-large shadow" alt="Tales from the Pit" src="title.svg" />
                </div>
                <Typography variant="h4" className={classes.chooseText}>
                    Choose a slot to load
                </Typography>
                <SaveGameSelector
                    enabledSlots={calculateEnabledSlots(saveTimes)}
                    currentSlot={currentSave}
                    saves={saveTimes}
                    onSelected={saveSelected}
                />
            </div>
        </KeyMaps>
    );
};

export default GameLoadMenu;
