import ArrayHelper from "../helpers/ArrayHelper";
import Hero from "../models/Hero";
import PartyStatistics from "../models/PartyStatistics";
import MockHeroService from "./MockHeroService";
import MockSaveService from "./MockSaveService";

export default class MockPartyService {
    getPartyStatistics(): PartyStatistics {
        return {
            reputation: 12,
            traits: ["Humiliated the Dread Pirate Roberts", "Gouged the eye from a Titan"],
        };
    }

    private static defaultHeroIds = (): string[] => [
        "gyvwain_the_chivalrous",
        "balthazar_the_unblemished",
        "gossamer",
        "gargomela_the_black",
    ];

    private static _heroIds: string[] | undefined = undefined;

    getParty(): Hero[] {
        const heroService = new MockHeroService();
        const party = this.getPartyIds().map((id) => heroService.getHeroDetail(id));
        return party;
    }

    getPartyIds(): string[] {
        if (MockPartyService._heroIds) {
            return MockPartyService._heroIds;
        }

        const saveService = new MockSaveService();
        const heroIds: string[] = saveService.getObject<string[]>("heroIds") ?? MockPartyService.defaultHeroIds();

        MockPartyService._heroIds = heroIds;

        return MockPartyService._heroIds;
    }

    setPartyIds(heroIds: string[]): void {
        MockPartyService._heroIds = heroIds;

        const saveService = new MockSaveService();
        saveService.saveObject("heroIds", heroIds);
    }

    private static defaultInventoryIds = (): string[] => ["wolf_spear", "ceramic_helm", "ceramic_armor"];

    private static defaultStashIds = (): string[] => ["hag_teeth", "token_of_servitude"];

    private static _inventoryItemIds: string[] | undefined = undefined;
    private static _stashItemIds: string[] | undefined = undefined;

    getInventory(): string[] {
        if (MockPartyService._inventoryItemIds) {
            return MockPartyService._inventoryItemIds;
        }

        const saveService = new MockSaveService();
        const inventoryIds: string[] = saveService.getObject<string[]>("inventoryItemIds") ?? MockPartyService.defaultInventoryIds();

        MockPartyService._inventoryItemIds = inventoryIds;

        return MockPartyService._inventoryItemIds;
    }

    setInventory(itemIds: string[]) {
        MockPartyService._inventoryItemIds = itemIds;

        const saveService = new MockSaveService();
        saveService.saveObject("inventoryItemIds", itemIds);
    }

    addInventoryItem(id: string) {
        const inventoryItems = this.getInventory();
        inventoryItems.push(id);
        this.setInventory(inventoryItems);
    }

    removeInventoryItem(id: string) {
        const inventoryItems = this.getInventory();
        ArrayHelper.remove(inventoryItems, id);
        this.setInventory(inventoryItems);
    }

    getStash(): string[] {
        if (MockPartyService._stashItemIds) {
            return MockPartyService._stashItemIds;
        }

        const saveService = new MockSaveService();

        const stashIds: string[] = saveService.getObject<string[]>("stashItemIds") ?? MockPartyService.defaultStashIds();

        MockPartyService._stashItemIds = stashIds;

        return MockPartyService._stashItemIds;
    }

    setStash(itemIds: string[]) {
        MockPartyService._stashItemIds = itemIds;

        const saveService = new MockSaveService();
        saveService.saveObject("stashItemIds", itemIds);
    }

    addStashItem(id: string) {
        const items = this.getStash();
        items.push(id);
        this.setStash(items);
    }

    removeStashItem(id: string) {
        const items = this.getStash();
        ArrayHelper.remove(items, id);
        this.setStash(items);
    }

    public clearCache() {
        MockPartyService._heroIds = undefined;
        MockPartyService._inventoryItemIds = undefined;
        MockPartyService._stashItemIds = undefined;
    }
}
