import React from "react";

// Services
import MockDetailedEncounterService from "../services/MockDetailedEncounterService";

// Pathfinding imports
import EncounterDetail from "../models/EncounterDetail";
import EncounterMap from "./EncounterMap";
import LoadingProgress from "./LoadingProgress";

type EncounterMapLoaderProps = {};

const EncounterMapLoader: React.FunctionComponent<EncounterMapLoaderProps> = (props: EncounterMapLoaderProps): JSX.Element => {
    
    let [encounter, setEncounter] = React.useState<EncounterDetail | null>(null);

    React.useEffect(() => {
        // Initial load of encounter data
        const loadEncounter = () => {
            const service = new MockDetailedEncounterService();
            const data = service.getEncounterDetail("tavern");
            if (!data) {
                console.error(`No map data`);
                return;
            }
            setEncounter(data);
        };
        if (encounter == null) {
            loadEncounter();
        }
    });

    if (!encounter) return <LoadingProgress />;

    return (
        <EncounterMap encounter={encounter} showGrid={true} />
    );
};

export default EncounterMapLoader;
