export default class MockSaveService {
    private static _currentSave = -1;

    public setCurrentSave = (slot: number) => {
        MockSaveService._currentSave = slot;
        localStorage.setItem("currentSlot", slot.toString());
    };

    public getCurrentSave = (): number => {
        if (MockSaveService._currentSave > 0) return MockSaveService._currentSave;
        const slot = localStorage.getItem("currentSlot");

        const slotId = slot ? Number.parseInt(slot) : 0;

        MockSaveService._currentSave = slotId;

        return slotId;
    };

    public markSlotUpdated = () => {
        localStorage.setItem(this.getCurrentSave() + "_time", new Date().toString());
    };

    public getSlotUpdateTimes = (): (Date | null)[] => {
        const slot1 = localStorage.getItem("0_time");
        const slot2 = localStorage.getItem("1_time");
        const slot3 = localStorage.getItem("3_time");

        const items: (Date | null)[] = [slot1 ? new Date(slot1) : null, slot2 ? new Date(slot2) : null, slot3 ? new Date(slot3) : null];

        return items;
    }

    public deleteSlot = (slot: number) => {

        if (slot === this.getCurrentSave()) {
            throw "Can't delete current save";
        }

        localStorage.removeItem(slot + "_time");
        localStorage.removeItem(slot + "_state");
    }

    public saveObject<T>(key: string, value: T) {
        localStorage.setItem(this.getCurrentSave() + "_" + key, JSON.stringify(value));
        this.markSlotUpdated();
    }

    public getObject<T>(key: string): T | undefined {
        const json = localStorage.getItem(this.getCurrentSave() + "_" + key);
        return json ? JSON.parse(json) : undefined;
    }
}
