import React from 'react';
import { useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core";

import AddCircleOutlineIcon from "@material-ui/icons/AddCircleOutline";
import SystemUpdateAltIcon from "@material-ui/icons/SystemUpdateAlt";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";

import LinkTo from '../components/LinkTo';
import KeyMaps from '../components/KeyMaps';
import ElectronHelper from '../helpers/ElectronHelper';
import MockSaveService from '../services/MockSaveService';
import MenuCards, { MenuCard } from '../components/MenuCards';

const useStyles = makeStyles({
    root: {
        backgroundColor: "#282c34",
        height: "100vh",
    },
    close: {
        filter: "invert(60%)",
    },
    cardIcon: {
        width: "100px",
        position: "absolute",
        fontSize: "50px",
    },
    titleContainer: {
        padding: "9vh",
    },
    appLogo: {
        height: "10vh",
        pointerEvents: "none",
        padding: "20px",
    },
});

const MainMenu: React.FunctionComponent = () => {

    const classes = useStyles();

    const [saveTimes, setSaveTimes] = React.useState<undefined | (Date | null)[]>(undefined);

    let history = useHistory();
    const navigateToNewGame = () => history.push("/game/new");
    const navigateToLoadGame = () => history.push("/game/load");
    const navigateToDeleteGame = () => history.push("/game/delete");

    React.useEffect(() => {
        // Initial load of hero data
        const loadSaves = () => {
            const saveService = new MockSaveService();
            setSaveTimes(saveService.getSlotUpdateTimes());
        };

        if (!saveTimes) {
            loadSaves();
        }
    });

    const onElectronExit = () => {
        const ipcRenderer = window.require("electron").ipcRenderer;
        ipcRenderer.send("close-app");
    }

    if (!saveTimes)
        return null;

    const slotCount = saveTimes.filter(s => s !== null).length;
    const allSlotsFull = slotCount >= 3;
    const oneSlotFull = slotCount === 1;

    const items: MenuCard[] = [
        {
            title: "New Game",
            description: "Start a new game" + (allSlotsFull ? " (All slots full)" : ""),
            enabled: !allSlotsFull,
            icon: <AddCircleOutlineIcon className={classes.cardIcon} />,
            onSelected: navigateToNewGame,
        },
        {
            title: "Load Game",
            description: "Load a game" + (oneSlotFull ? " (Only one slot in use)" : ""),
            enabled: !oneSlotFull,
            icon: <SystemUpdateAltIcon className={classes.cardIcon} />,
            onSelected: navigateToLoadGame,
        },
        {
            title: "Delete Game",
            description: "Delete a game" + (oneSlotFull ? " (Only one slot in use)" : ""),
            enabled: !oneSlotFull,
            icon: <DeleteOutlineIcon className={classes.cardIcon} />,
            onSelected: navigateToDeleteGame,
        },
    ];

    // Add a quit option if running under Electron
    if (ElectronHelper.isElectron()) {
        items.push({
            title: "Quit",
            description: "Exit the game",
            enabled: true,
            icon: <ExitToAppIcon className={classes.cardIcon} />,
            onSelected: onElectronExit,
        });
    }
    
    return (
        <KeyMaps backUrl="/">
            <div className={classes.root}>
                <LinkTo url={`${process.env.PUBLIC_URL}/`} top="20px" left="20px" width="40px">
                    <img src="images/close.svg" className={classes.close} alt="close" />
                </LinkTo>
                <div className={classes.titleContainer}>
                    <img src="./volcano.svg" className={classes.appLogo} alt="logo" />
                    <img className="title-large shadow" alt="Tales from the Pit" src="title.svg" />
                </div>
                <MenuCards items={items} />
            </div>
        </KeyMaps>
    );
}

export default MainMenu;
