import React from 'react';

// Models
import GameState from '../models/GameState';
import { MapGraph } from '../models/MapGraph';

// Services
import MockMapService from "../services/MockMapService";
import GraphBuilder from "../services/GraphBuilder";
import OverheadMap from './OverheadMap';
import MockGameStateService from '../services/MockGameStateService';
import LoadingProgress from './LoadingProgress';

type OverheadMapProps = {
    startState: GameState;
};

const OverheadMapLoader: React.FunctionComponent<OverheadMapProps> = (props: OverheadMapProps) => {

    const { startState } = props;

    let [graph, setGraph] = React.useState<MapGraph | undefined>(undefined); // The map graph data
    let [pathColor, setPathColor] = React.useState<string | undefined>(undefined);

    let [mapUrl, setMapUrl] = React.useState<string | undefined>(undefined);
    let [gameState, setGameState] = React.useState<GameState>(startState);

    React.useEffect(() => {
        // Initial load of map data
        const loadMap = () => {
            const service = new MockMapService();

            const data = service.getMap(gameState.currentMapId);
            if (!data) {
                console.error(`No map data`);
                return;
            }

            console.log(
                `Loaded map : ${gameState.currentMapId}, ${gameState.currentVertextId}. ${data?.vertices.length} vertices. Path Color ${
                    data.pathColor ?? "None"
                }`
            );

            setMapUrl(data.imageUrl);
            setPathColor(data.pathColor ?? "#5E4E00"); // The default path colour is brown

            const mapGraph = GraphBuilder.Build(data);
            setGraph(mapGraph);
        };

        if (!graph) {
            loadMap();
        }
    });

    const onNewMap = (newState: GameState) => {

        console.log("New map : " + newState.currentMapId + ", " + newState.currentVertextId);

        // Save the new map state
        const stateService = new MockGameStateService();
        stateService.setState(newState);
        setGameState(newState);

        // Force a reload of the map
        // Need to set the graph last, or the load can happen after the useEffect
        setMapUrl(undefined);
        setPathColor(undefined);
        setGraph(undefined);
    };

    if (!mapUrl || !graph || !pathColor) return <LoadingProgress />;

    return <OverheadMap startState={gameState} graph={graph} mapUrl={mapUrl} pathColor={pathColor} onNewMap={onNewMap} />;
};

export default OverheadMapLoader;
