import React from "react";
import { Card, CardHeader, CardContent, Grid, makeStyles, Typography } from "@material-ui/core";


const useStyles = makeStyles({
    container: {
        top: "1vh",
        position: "relative",
    },
    card: {
        position: "relative",
        margin: "35px",
        cursor: "pointer",
        backgroundColor: "lightsteelblue",
        "&:hover": {
            top: "-5px",
            right: "5px",
            boxShadow: "10px 10px",
            backgroundColor: "ghostwhite",
        },
    },
    cardDisabled: {
        position: "relative",
        margin: "35px",
        cursor: "not-allowed",
        backgroundColor: "darkgrey",
    },
    cardActive: {
        position: "relative",
        margin: "35px",
        cursor: "not-allowed",
        backgroundColor: "darkseagreen",
    },
});

export type MenuCard = {
    title: string;
    description: string;
    enabled: boolean;
    active?: boolean;
    icon?: JSX.Element;
    onSelected: () => void;
};

type MenuCardsProps = {
    items: MenuCard[];
};

const MenuCards: React.FunctionComponent<MenuCardsProps> = (props: MenuCardsProps) => {
    const classes = useStyles();

    const selectClass = (i: MenuCard) => (i.active ? classes.cardActive : i.enabled ? classes.card : classes.cardDisabled);

    const { items } = props;
    const renderCards = () =>
        items.map((i) => (
            <Card className={selectClass(i)} onClick={i.onSelected}>
                <CardHeader title={<Typography variant="h2">{i.title}</Typography>} avatar={i.icon} />
                <CardContent>{i.description}</CardContent>
            </Card>
        ));

    return (
        <Grid container className={classes.container}>
            <Grid item xs={3}></Grid>
            <Grid item xs={6}>
                {renderCards()}
            </Grid>
            <Grid item xs={3}></Grid>
        </Grid>
    );
};

export default MenuCards;
