import React from "react";

// UI Elements
import { CRS, latLng, LatLng, LatLngBounds, LatLngExpression, LatLngTuple } from "leaflet";
import { Map, ImageOverlay, Polygon } from "react-leaflet";
import EncounterDialog from "../components/EncounterDialog";

// Models
import EncounterDetail from "../models/EncounterDetail";

type EncounterMapProps = {
    encounter: EncounterDetail;
    showGrid: boolean;
};

const EncounterMap: React.FunctionComponent<EncounterMapProps> = (props: EncounterMapProps): JSX.Element => {

    const { encounter, showGrid } = props;

    let [pos, setPos] = React.useState<LatLng[] | undefined>(undefined); // The current (target) position of the hero markers
    let [encounterId, setEncounterId] = React.useState<string | null>(null);

    // TODO: Need the following:
    // A grid of points that the payer can move to, and grid of blocked points
    // A different library for grid-based pathfinding
    // Define the hero starting points on the encounter

    // Fire when user clicks on the map
    const handleClick = (e: any) => {
        //if (!graph) return; // Map not loaded

        console.log("clicked at: " + e.latlng);
    };

    const handleEncounterClose = (moveTo: string | undefined) => {
        console.log("Close encounter");
        setEncounterId(null);
    };

    React.useEffect(() => {
        // Initial load of map data
    });

    const bounds = new LatLngBounds(latLng([0.0, 0.0]), latLng([1609, 2143]));

    const gridPolygons: JSX.Element[] = [];

    const { gridOffset, gridScale } = encounter;

    // Conversion functions from isometric to cardinal co-ordinates
    const isoX = (carX: number, carY: number): number => carX + carY;
    const isoY = (carX: number, carY: number): number => carY - carX / 2.0;

    const carX = (isoX: number, isoY: number): number => (isoX - isoY) / 1.5;
    const carY = (isoX: number, isoY: number): number => isoX / 3.0 + isoY / 1.5;

    const iso = (car: LatLngTuple): LatLng => latLng(isoX(car[0], car[1]), isoY(car[0], car[1]));
    const car = (iso: LatLngTuple): LatLng => latLng(carX(iso[0], iso[1]), carY(iso[0], iso[1]));
    const offset = (iso: LatLng, offset: LatLngTuple): LatLng => latLng(iso.lat + offset[0], iso.lng + offset[1]);
    const scale = (iso: LatLng, scale: LatLngTuple): LatLng => latLng(iso.lat * scale[0], iso.lng * scale[1]);

    if (showGrid) {
        for (let x = 0; x < encounter.grid.length; x++) {
            for (let y = 0; y < encounter.grid.length; y++) {
                const gridPoly = (
                    <Polygon
                        positions={[
                            offset(scale(iso([x, y]), gridScale), gridOffset),
                            offset(scale(iso([x + 1, y]), gridScale), gridOffset),
                            offset(scale(iso([x + 1, y + 1]), gridScale), gridOffset),
                            offset(scale(iso([x, y + 1]), gridScale), gridOffset),
                        ]}
                        color={encounter.grid[x][y] === "1" ? "green" : "red"}
                    />
                );
                gridPolygons.push(gridPoly);
            }
        }
    }

    return (
        <Map crs={CRS.Simple} zoom={0} bounds={bounds} maxBounds={bounds} scrollWheelZoom={false} onClick={handleClick} zoomControl={false}>
            <EncounterDialog onClose={handleEncounterClose} encounterId={encounterId} />
            <ImageOverlay url={encounter.imageUrl} bounds={bounds} />
            {gridPolygons}
            <Polygon
                positions={[
                    [37, 109.05],
                    [141, 109.03],
                    [141, 202.05],
                    [37, 202.04],
                ]}
                color={"white"}
            />
        </Map>
    );
};

export default EncounterMap;
