import React from "react";
import { makeStyles } from "@material-ui/core";

import SaveIcon from "@material-ui/icons/Save";

import MenuCards, { MenuCard } from "./MenuCards";

const useStyles = makeStyles({
    cardIcon: {
        width: "100px",
        position: "absolute",
        fontSize: "50px",
    },
});

type SaveGameSelectorProps = {
    enabledSlots: boolean[];
    saves: (Date | null)[];
    currentSlot: number;
    onSelected: (slot: number) => void;
};

const SaveGameSelector: React.FunctionComponent<SaveGameSelectorProps> = (props: SaveGameSelectorProps) => {
    const classes = useStyles();
    
    const { saves, enabledSlots, currentSlot, onSelected } = props;

    const getDateString = (date: Date | null) => (date ? `${date.toLocaleString()}` : "Empty");
    const getDescription = (date: Date | null, isCurrentSlot: boolean) => {
        return isCurrentSlot ? `Current Save (${getDateString(date)})` : getDateString(date);
    };

    const saveButtons: MenuCard[] = [
        {
            title: "Slot A",
            description: getDescription(saves[0], currentSlot === 0),
            enabled: enabledSlots[0],
            active: currentSlot === 0,
            onSelected: () => onSelected(0),
            icon: <SaveIcon className={classes.cardIcon} />,
        },
        {
            title: "Slot B",
            description: getDescription(saves[1], currentSlot === 1),
            enabled: enabledSlots[1],
            active: currentSlot === 1,
            onSelected: () => onSelected(1),
            icon: <SaveIcon className={classes.cardIcon} />,
        },
        {
            title: "Slot C",
            description: getDescription(saves[2], currentSlot === 2),
            enabled: enabledSlots[2],
            active: currentSlot === 2,
            onSelected: () => onSelected(2),
            icon: <SaveIcon className={classes.cardIcon} />,
        },
    ];

    return <MenuCards items={saveButtons} />;
};

export default SaveGameSelector;
