import EncounterDetail from "../models/EncounterDetail";

import TavernMap from "./encounter-maps/tavern.json";

export default class MockDetailedEncounterService {
    public getEncounterDetail(id: string): EncounterDetail | null {
        let json: any = null;

        switch (id) {
            case "tavern":
            default:
                json = TavernMap;
                break;
        }

        return json
    }
}
