import GameState from "../models/GameState";
import MockSaveService from "./MockSaveService";

export default class MockGameStateService {

    private static defaultState = (): GameState => {
        return {
            currentMapId: "town",
            currentVertextId: "hut-road", // "scupperden-road"
        }
    };

    private static _state: GameState | undefined = undefined;

    public getState(): GameState {
        if (MockGameStateService._state != null) 
            return MockGameStateService._state;
            
        const saveService = new MockSaveService();
        
        const stateJson = localStorage.getItem(saveService.getCurrentSave() + "_state");
        const state: GameState = stateJson ? JSON.parse(stateJson) : MockGameStateService.defaultState();

        MockGameStateService._state = state;

        return state;
    }

    public setState(state: GameState) {
        const saveService = new MockSaveService();

        MockGameStateService._state = state;
        console.log("setState : " + JSON.stringify(state));
        localStorage.setItem(saveService.getCurrentSave() + "_state", JSON.stringify(state));
        saveService.markSlotUpdated();
    }

    public clearCache() {
        MockGameStateService._state = undefined;
    }
}
