import "./App.scss";

import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";
import { ThemeProvider } from "@material-ui/core";

import PitTheme from "./PitTheme";

// Pages
import MainMenu from "./pages/MainMenu";
import MainLayout from "./MainLayout";
import GameNewMenu from "./pages/GameNewMenu";
import GameLoadMenu from "./pages/GameLoadMenu";
import GameDeleteMenu from "./pages/GameDeleteMenu";

const App : React.FunctionComponent = () => {

    return (
        <div className="app">
            <ThemeProvider theme={PitTheme}>
                <HashRouter>
                    <Switch>
                        <Route path={`${process.env.PUBLIC_URL}/menu`} component={MainMenu} />
                        <Route path={`${process.env.PUBLIC_URL}/game/new`} component={GameNewMenu} />
                        <Route path={`${process.env.PUBLIC_URL}/game/load`} component={GameLoadMenu} />
                        <Route path={`${process.env.PUBLIC_URL}/game/delete`} component={GameDeleteMenu} />
                        <Route path={`${process.env.PUBLIC_URL}/`} component={MainLayout} />
                    </Switch>
                </HashRouter>
            </ThemeProvider>
        </div>
    );
}

export default App;
